import gsap from "gsap";

export function initLoader() {
  let mm = gsap.matchMedia();

  mm.add(
    {
      isMobile: "(max-width: 768px)",
      isDesktop: "(min-width: 769px)",
    },
    (context) => {
      const windowTop = window.innerHeight;
      const itemMenu = document.querySelector(".gsap_menu_sPrincipal");
      const itemMain = document.querySelector(".gsap_main_sPrincipal");
      const itemArticle = document.querySelector(".gsap_article_sPrincipal");

      let { isMobile, isDesktop } = context.conditions;
      let duration = context.isReverted ? 0 : 1.2;

      let tl = gsap.timeline({ paused: true });
      tl.set("body", { overflow: "hidden" })
        .to(".loader", {
          yPercent: -100,
          duration: duration,
          borderBottomLeftRadius: isDesktop ? "100%" : "50%",
          borderBottomRightRadius: isDesktop ? "100%" : "50%",
          ease: "expo.inOut",
        })
        .from(
          itemMenu,
          {
            yPercent: 50,
            ease: "back.out(4)",
            duration: 0.9,
            stagger: 0.09,
          },
          "<0.5"
        )
        .from(
          itemMain,
          {
            yPercent: 5,
            ease: "back.out(4)",
            duration: 0.9,
            stagger: 0.09,
          },
          "<"
        )
        .from(
          itemArticle,
          {
            yPercent: 50,
            ease: "back.out(4)",
            duration: 0.9,
            stagger: 0.09,
          },
          "<"
        )
        .set("body", { overflow: "auto" });

      const triggerAnimation = () => {
        if (window.scrollY <= windowTop) {
          console.log("iniciou");
          tl.play();
        } else {
          console.log("pausou");
          tl.pause();
          gsap.set(".loader", { display: "none" });
        }
      };

      triggerAnimation();
    }
  );
}
