import Lottie from "lottie-web";
import jsonLampada from "./lampada.json";
import jsonRaio from "./raio.json";
import jsonEconomia from "./moeda.json";

export function initLottie() {
  const motionEconomia = document.querySelector(
    ".gsap_icon_beneficios.economia"
  );
  const motionRaio = document.querySelector(".gsap_icon_beneficios.raio");
  const motionLampada = document.querySelector(".gsap_icon_beneficios.lampada");
  let autoplay = true;
  if (motionEconomia) {
    const animEconomia = Lottie.loadAnimation({
      container: motionEconomia,
      renderer: "svg",
      loop: true,
      autoplay: autoplay,
      animationData: jsonEconomia,
    });

    if (motionRaio) {
      const animRaio = Lottie.loadAnimation({
        container: motionRaio,
        renderer: "svg",
        loop: true,
        autoplay: autoplay,
        animationData: jsonRaio,
      });
    } else {
      console.error("Elemento motionRaio não encontrado.");
    }

    if (motionLampada) {
      const animLampada = Lottie.loadAnimation({
        container: motionLampada,
        renderer: "svg",
        loop: true,
        autoplay: autoplay,
        animationData: jsonLampada,
      });
    } else {
      console.error("Elemento motionLampada não encontrado.");
    }
  } else {
    console.error("Elemento motionEconomia não encontrado.");
  }
}
