import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Lottie from "lottie-web";
import jsonBateria from "./bateria.json";
import jsonBio from "./bio.json";
import jsonEolica from "./eolica.json";
import jsonSolar from "./solar.json";

export function initsServicos() {
  gsap.registerPlugin(ScrollTrigger);

  // Seletores para cada animação
  const motionEolica = document.querySelector(".gsap_servicos_eolica");
  const motionBateria = document.querySelector(".gsap_servicos_bateria");
  const motionBio = document.querySelector(".gsap_servicos_bio");
  const motionSolar = document.querySelector(".gsap_servicos_solar");

  // Inicialização das animações Lottie
  let animEolica, animBateria, animBio, animSolar;

  if (motionEolica) {
    animEolica = Lottie.loadAnimation({
      container: motionEolica,
      renderer: "svg",
      loop: true,
      autoplay: false,
      animationData: jsonEolica,
    });
  }

  if (motionBateria) {
    animBateria = Lottie.loadAnimation({
      container: motionBateria,
      renderer: "svg",
      loop: true,
      autoplay: false,
      animationData: jsonBateria,
    });
  }

  if (motionBio) {
    animBio = Lottie.loadAnimation({
      container: motionBio,
      renderer: "svg",
      loop: true,
      autoplay: false,
      animationData: jsonBio,
    });
  }
  if (motionSolar) {
    animSolar = Lottie.loadAnimation({
      container: motionSolar,
      renderer: "svg",
      loop: true,
      autoplay: false,
      animationData: jsonSolar,
    });
  } else {
    console.error("Elemento .gsap_servicos_solar não encontrado.");
  }

  // Configuração de escala dinâmica e tempo
  const scaleMax = gsap.utils.mapRange(
    1,
    document.querySelectorAll(".cards").length - 1,
    0.9,
    1
  );
  const time = 2;

  gsap.set(".cards", {
    y: (index) => 0 * index,
  });

  // Timeline GSAP com ScrollTrigger
  let tl = gsap.timeline({
    scrollTrigger: {
      trigger: ".cards-container",
      start: "center center",
      end: "+=2000px top",
      scrub: 1,
      pin: true,
      snap: 0.2,
    },
  });

  // Iniciar a animação de Lottie no início do ScrollTrigger
  tl.add(() => {
    if (animBateria) animBateria.play();
    if (animEolica) animEolica.play(); // Lottie Eólica começa a tocar
    if (animBio) animBio.play(); // Lottie Bio começa a tocar
    if (animSolar) animSolar.play(); // Lottie Bio começa a tocar
  }, 0);

  // Animação dos cartões
  tl.from(".cards:not(:first-child)", {
    y: () => window.innerHeight,
    duration: time / 2,
    stagger: time,
  });

  tl.to(
    ".cards:not(:last-child)",
    {
      scale: (index) => scaleMax(index), // dinamicamente ajusta o scale baseado no índice
      filter: "blur(1px)",
      stagger: {
        each: time,
      },
    },
    time // Inicia quando a primeira animação de cartas terminar
  );

  // Parar as animações Lottie quando a timeline acabar (opcional)
  tl.add(() => {
    if (animBateria) animBateria.stop(); // Para a animação Lottie Bateria
    if (animEolica) animEolica.stop(); // Para a animação Lottie Eólica
    if (animBio) animBio.stop(); // Para a animação Lottie Bio
    if (animSolar) animSolar.stop(); // Lottie Bio começa a tocar
  }, "+=1");
}
