import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
export function initsCliente() {
  gsap.registerPlugin(ScrollTrigger);
  const item1 = document.querySelector(".gsap_article_cliente");
  const item2 = document.querySelector(".gsap_text_cliente");
  const item3 = document.querySelector(".gsap_cliente_cliente");

  let tl = gsap.timeline({
    scrollTrigger: {
      trigger: "#clientes",
      start: "20% bottom",
      end: "bottom bottom",
      onEnter: () => tl.restart(),
      onEnterBack: () => tl.play(),
    },
  });

  tl.from(item1, {
    yPercent: 50,
    ease: "back.out(4)",
    duration: 0.9,
    stagger: 0.09,
  })
    .from(
      item2,
      {
        yPercent: 10,
        ease: "back.out(4)",
        duration: 0.9,
        stagger: 0.09,
      },
      "<0.1"
    )
    .from(
      item3,
      {
        yPercent: 50,
        ease: "back.out(4)",
        duration: 0.9,
        stagger: 0.09,
      },
      "<0.2"
    );
}
