import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import SplitType from "split-type";

export function initsCta() {
  let mm = gsap.matchMedia();

  mm.add(
    {
      isMobile: "(max-width: 768px)", // Condição para mobile
      isDesktop: "(min-width: 769px)", // Condição para desktop
    },
    (context) => {
      const text = new SplitType(".split_cta", { types: "words" });
      const result = gsap.utils.toArray(".split_cta .word");
      const item1 = document.querySelector(".gsap_cta_item1");
      const item2 = document.querySelector(".gsap_cta_item2");

      let { isMobile, isDesktop } = context.conditions;
      let endValue = isMobile ? "500px" : "500px";

      let tl = gsap.timeline({
        scrollTrigger: {
          trigger: "#cta",
          start: "center center",
          end: `+=${endValue} center`,
          scrub: 1,
          pin: true,
        },
      });

      tl.from(result, {
        autoAlpha: 0,
        yPercent: 100,
        duration: 1,
        ease: "back.out(4)",
        stagger: {
          each: 0.009,
        },
      })
        .from(
          item1,
          {
            yPercent: 50,
            autoAlpha: 0,

            ease: "back.out(4)",
            duration: 0.9,
            stagger: 0.09,
          },
          "<0.1"
        )
        .from(
          item2,
          {
            yPercent: 50,
            autoAlpha: 0,
            ease: "back.out(4)",
            duration: 0.9,
            stagger: 0.09,
          },
          "<0.1"
        );
    }
  );
}
