import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

export function initsFAq() {
  const faqButtons = document.querySelectorAll(".gsap_faq_button");

  faqButtons.forEach((button) => {
    button.addEventListener("click", () => {
      const content = button.querySelector(".faq"); // Seleciona o conteúdo dentro do botão clicado
      const isOpen = content.classList.contains("ativo");

      if (isOpen) {
        // Animação para fechar o FAQ
        gsap.to(content, {
          autoAlpha: 0,
          height: 0,
          duration: 0.3,
          ease: "power2.out",
          onComplete: () => {
            content.classList.remove("ativo");
            content.classList.add("hidden");
          },
        });
      } else {
        // Primeiro, fecha todos os FAQs abertos
        const allFaqs = document.querySelectorAll(".faq.ativo");
        allFaqs.forEach((openFaq) => {
          gsap.to(openFaq, {
            autoAlpha: 0,
            height: 0,
            duration: 0.3,
            ease: "power2.out",
            onComplete: () => {
              openFaq.classList.remove("ativo");
              openFaq.classList.add("hidden");
            },
          });
        });

        // Animação para abrir o FAQ clicado
        content.classList.remove("hidden");
        const height = content.scrollHeight;

        gsap.fromTo(
          content,
          { height: 0 },
          {
            height: height,
            autoAlpha: 1,
            duration: 0.3,
            ease: "power2.out",
            onComplete: () => {
              content.classList.add("ativo");
            },
          }
        );
      }
    });
  });
}
