import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import SplitType from "split-type";

export function initsSobre() {
  const item1 = document.querySelector(".gsap_sobre_item1");
  const item2 = document.querySelector(".gsap_sobre_item2");
  const linhas = gsap.utils.toArray(".gsap_sobre_linha");
  const titulos = new SplitType(".split_sobre_titulos", { types: "chars" });
  const descricoes = new SplitType(".split_sobre_descricoes", {
    types: "lines",
  });

  let tl = gsap.timeline({
    scrollTrigger: {
      trigger: "#sobre",
      start: "top bottom",
      end: "bottom bottom",
      onEnter: () => tl.restart(),
      onEnterBack: () => tl.play(),
    },
  });

  tl.from(item1, {
    yPercent: 50,
    ease: "back.out(4)",
    duration: 0.9,
    stagger: 0.09,
    autoAlpha: 0,
  })
    .from(
      item2,
      {
        yPercent: 50,
        ease: "back.out(4)",
        duration: 0.9,
        stagger: 0.09,
        autoAlpha: 0,
      },
      "<0.1"
    )
    .from(
      titulos.chars,
      {
        autoAlpha: 0,
        yPercent: 100,
        duration: 1,
        ease: "back.out(4)",
        stagger: {
          each: 0.009,
        },
      },
      "<"
    )
    .from(
      descricoes.lines,
      {
        autoAlpha: 0,
        yPercent: 100,
        duration: 1,
        ease: "back.out(4)",
        stagger: {
          each: 0.009,
        },
      },
      "<0.2"
    )
    .from(
      linhas,
      {
        width: "0%",
        duration: 0.5,
        autoAlpha: 0,
        stagger: {
          each: 0.09,
        },
      },
      "<0.1"
    );
}
