import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import SplitType from "split-type";
import { initLottie } from "./lottie";

gsap.registerPlugin(ScrollTrigger);

export function initsBeneficios() {
  const item1 = document.querySelector(".gsap_article_beneficios");
  const item2 = document.querySelector(".gsap_parceiros_beneficios");
  const icons = gsap.utils.toArray(".gsap_icon_beneficios");
  const titulos = [
    new SplitType(".split_beneficios_titulo1", { types: "chars" }).chars,
    new SplitType(".split_beneficios_titulo2", { types: "chars" }).chars,
    new SplitType(".split_beneficios_titulo3", { types: "chars" }).chars,
  ];
  const descricoes = [
    new SplitType(".split_beneficios_descricao1", { types: "lines" }).lines,
    new SplitType(".split_beneficios_descricao2", { types: "lines" }).lines,
    new SplitType(".split_beneficios_descricao3", { types: "lines" }).lines,
  ];

  let tl = gsap.timeline({
    scrollTrigger: {
      trigger: "#beneficios",
      start: "20% bottom",
      end: "bottom bottom",
      onEnter: () => tl.restart(),
      onEnterBack: () => tl.play(),
    },
  });

  tl.from(item1, {
    yPercent: 50,
    ease: "back.out(4)",
    duration: 0.9,
    stagger: 0.09,
    autoAlpha: 0,
  })
    .from(
      icons,
      {
        yPercent: 50,
        ease: "back.out(4)",
        duration: 0.9,
        stagger: 0.09,
        autoAlpha: 0,
      },
      "<"
    )
    .from(
      item2,
      {
        yPercent: 50,
        autoAlpha: 0,
        ease: "back.out(4)",
        duration: 0.9,
        stagger: 0.09,
      },
      "<0.1"
    );

  titulos.forEach((titulo, index) => {
    tl.from(
      titulo,
      {
        autoAlpha: 0,
        yPercent: 100,
        duration: 1,
        ease: "back.out(4)",
        stagger: {
          each: 0.009,
        },
      },
      "<0.1"
    ).from(
      descricoes[index],
      {
        autoAlpha: 0,
        yPercent: 50,
        duration: 1,
        ease: "back.out(4)",
        stagger: {
          each: 0.009,
        },
      },
      "<0.1"
    );
  });
}
