import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import SplitType from "split-type";

export function initsMensagem() {
  let mm = gsap.matchMedia();

  mm.add(
    {
      isMobile: "(max-width: 768px)", // Condição para mobile
      isDesktop: "(min-width: 769px)", // Condição para desktop
    },
    (context) => {
      const text = new SplitType(".split_mensagem_1", { types: "words" });
      const text2 = new SplitType(".split_mensagem_2", { types: "words" });
      const result = gsap.utils.toArray(".split_mensagem_1 .word");
      const result2 = gsap.utils.toArray(".split_mensagem_2 .word");

      let { isMobile, isDesktop } = context.conditions;
      let endValue = isMobile ? "1500px" : "3000px";

      let tl = gsap.timeline({
        scrollTrigger: {
          trigger: ".gsap_container_sMensagem",
          start: "center center",
          end: `+=${endValue} center`,
          scrub: 1,
          pin: true,
        },
      });

      tl.from(result, {
        autoAlpha: 0,
        yPercent: 100,
        duration: 1,
        ease: "back.out(4)",
        stagger: {
          each: 0.009,
        },
      })
        .to(result, {
          autoAlpha: 0,
        })
        .to(result, {
          display: "none",
        })
        .from(result2, {
          autoAlpha: 0,
          yPercent: 100,
          duration: 1,
          ease: "back.out(4)",
          stagger: {
            each: 0.009,
          },
        });
    }
  );
}
